cc.Class({
    extends: cc.Component,

    properties: {
        //在那个区域描摹
        guijiNode: {
            default: null,
            type: cc.Node,
            tooltip: '轨迹所在区域'
        },
        //描摹的画笔
        graphicsNode: {
            default: null,
            type: cc.Node,
            tooltip: '用来描摹的画笔'
        },
        graphicsNode2: {
            default: null,
            type: cc.Node,
            tooltip: '用来描摹的画笔'
        },
        handNode: {
            default: null,
            type: cc.Node,
            tooltip: '轨迹所在区域'
        },
        handNode1: {
            default: null,
            type: cc.Node,
            tooltip: '轨迹所在区域'
        },
        handNode2: {
            default: null,
            type: cc.Node,
            tooltip: '轨迹所在区域'
        },
        handIconNode: {
            default: null,
            type: cc.Node,
            tooltip: '手指'
        },
        miaomoNum: 0,
        particlePrefab: cc.Prefab,
        stepCountMax: 3,
        stepCount: 3,
        step: 1,
        gameOver: false,
    },

    onLoad() {
        //获取描摹2需要的轨迹点
        this.arr = this.guijiNode.getComponent("guiji").getXYPoints(this.miaomoNum);
        if (this.miaomoNum==0) {
            this.schedule(this.updateGuide, 10);
        }
    },

    onDestroy() {
        // 当这个节点被销毁时，取消定时器
        this.unschedule(this.updateGuide);
    },

    //设置箭头方向（包括描摹过程中箭头的方向）
    initAngle() {
        // let angle = this.getAngle(this.arr[0].x, this.arr[0].y, this.arr[2].x, this.arr[2].y);
        // this.node.rotation = -angle;
    },

    start() {
        if (this.arr.length === 0) {
            return;
        }
        this.initAngle();
        this.init();
    },

    init() {
        let node = this.node;
        node.x = this.arr[1].x;
        node.y = this.arr[1].y;
        this.handTween();

        this.gameOver = false;
        this.arrIndex = 1;

        node.on(cc.Node.EventType.TOUCH_START, (event) => {
            this.touchBegin = true;
            // this.handNode.active = false;
            this.unschedule(this.updateStep);
            this.unschedule(this.updateBackStep);
            this.handTweenStop();
        }, node);

        node.on(cc.Node.EventType.TOUCH_MOVE, (event) => {
            if (!this.touchBegin) {
                return
            }

            let nodeWorldPos = this.node.parent.convertToWorldSpaceAR(cc.v2(this.node.x, this.node.y));
            let position = event.touch.getLocation();
            cc.log("position" + position.x, position.y);
            cc.log("nodeWorldPos" + nodeWorldPos.x, nodeWorldPos.y);

            this.move(cc.v2(position.x - nodeWorldPos.x, position.y - nodeWorldPos.y));
        }, node);

        node.on(cc.Node.EventType.TOUCH_END, (event) => {
            this.touchBegin = false;
            this.handTween();
        }, node);

        node.on(cc.Node.EventType.TOUCH_CANCEL, (event) => {
            this.touchBegin = false;
            this.handTween();
        }, node);
    },

    handTween() {
        if (this.gameOver) return
        this.handIconNode.opacity = 255;
        const forward = cc.tween().parallel(
            cc.tween().to(1, {scale: 0.5}, {easing: 'sineOut'}),
            cc.tween().to(1, {y: -5}, {easing: 'quadOut'}))
        const backward = cc.tween().parallel(
            cc.tween().to(1, {scale: 1}, {easing: 'sineIn'}),
            cc.tween().to(1, {y: 0}, {easing: 'quadIn'}))
        const out = cc.tween().parallel(
            cc.tween().to(0.5, {scale: 0.5}, {easing: 'sineOut'}),
            cc.tween().to(0.5, {opacity: 0}, {easing: 'sineOut'}),
            cc.tween().to(1, {y: 0}, {easing: 'quadIn'}))
        cc.tween(this.handIconNode)
            .sequence(forward, backward, forward, backward, out)
            .start();
    },

    handTweenStop() {
        // 停止所有正在运行的tween动画
        this.stopAllActions();
        cc.tween(this.handIconNode).parallel(
            cc.tween().to(0.01, {scale: 1}, {easing: 'sineIn'}),
            cc.tween().to(0.01, {opacity: 255}, {easing: 'quadIn'}),
            cc.tween().to(0.01, {y: 0}, {easing: 'quadIn'}))
        .call(() => {
            this.handIconNode.stopAllActions(); 
        })
        .start();
    },

    handShow(callback) {
        this.stopAllActions();
        cc.tween(this.handIconNode).parallel(
            cc.tween().to(0.5, {scale: 1}, {easing: 'sineIn'}),
            cc.tween().to(0.5, {opacity: 255}, {easing: 'quadIn'}),
            cc.tween().to(0.5, {y: 0}, {easing: 'quadIn'}))
        .call(callback)
        .start();
    },

    stopAllActions() {
        this.handIconNode.stopAllActions();
        this.handIconNode.opacity = 0;
    },

    //箭头复原动画
    interruptAnm(position) {
        let action = cc.sequence(
            cc.moveBy(0.2, position.x, position.y),
            cc.moveBy(0.2, -position.x, -position.y)
        );
        this.node.runAction(action);
    },

    //获取描摹过程中，箭头要跟着轨迹旋转的角度let
    getAngle(x1, y1, x2, y2) {
        // 直角的边长
        let x = Math.abs(x1 - x2);
        let y = Math.abs(y1 - y2);
        let z = Math.sqrt(x * x + y * y);
        let angle = Math.round((Math.asin(y / z) / Math.PI * 180));

        if (y2 > y1 && x2 < x1) {
            angle = 180 - angle;
        } else if (y2 < y1 && x2 < x1) {
            angle = 180 + angle;
        } else if (y2 < y1 && x2 > x1) {
            angle = 360 - angle;
        }
        return angle;
    },

    //根据轨迹坐标点描摹
    move(position) {
        if (this.gameOver) {
            cc.log("游戏已经结束，顺利的描摹出来2了 !!!");
            return;
        }
        let dx = position.x + this.node.x;
        let dy = position.y + this.node.y;

        let node_d = (position.x) * (position.x) + (position.y) * (position.y)
        if (node_d > 120 * 120) {
            cc.log(" Beyond the scope Beyond the scope Beyond the scope ");
            this.interruptAnm(position);
            this.touchBegin = false;
            return;
        }

        let range = 20;
        let distance = 35;
        let max = this.arrIndex + range;
        if (max >= this.arr.length) {
            // this.touchBegin = false
            // this.gameOver = true
            // cc.log("game over ")
            // return
            max = this.arr.length;
        }
        for (let index = this.arrIndex; index < max; index++) {
            // let index = max
            const element = this.arr[index];
            // cc.log("index  "+index)
            let d = (dx - element.x) * (dx - element.x) + (dy - element.y) * (dy - element.y)
            if (d < distance * distance) {
                this.graphicsNode.getComponent('mmGraphics').setPos(index);
                this.arrIndex = index;
                this.node.x = element.x;
                this.node.y = element.y;

                if (index === this.arr.length - 1) {
                    this.drawEnd();
                } else {
                    // let angle = this.getAngle(this.arr[index].x, this.arr[index].y, this.arr[index + 1].x, this.arr[index + 1].y);
                    // this.node.rotation = -angle;
                }
            }
        }
    },

    //演示引导
    updateGuide: function (dt) {
        cc.log('updateGuide touchBegin = ' + this.touchBegin);
        if (this.touchBegin) return;
        this.stepCount = this.arrIndex+this.stepCountMax;
        this.step = this.arrIndex;
        let max = this.arr.length;
        if (this.stepCount > max) {
            this.stepCount = max;
        }

        this.handShow(() => {
            this.schedule(this.updateStep, 0.25);
        });
    },

    updateStep: function (dt) {
        cc.log('updateStep touchBegin = ' + this.touchBegin);
        if (this.touchBegin) return;
        // let index = max
        let index = this.step;
        const element = this.arr[index];
        cc.log("updateStep index  "+index)
        this.graphicsNode.getComponent('mmGraphics').setPos(index);
        this.node.x = element.x;
        this.node.y = element.y;
        this.step = ++index;
        cc.log("updateStep step  " + this.step + " , stepCount = " + this.stepCount);
        if (this.step>=this.stepCount) {
            this.unschedule(this.updateStep);
            this.schedule(this.updateBackStep, 0.25);
        }
    },

    updateBackStep: function (dt) {
        cc.log('updateBackStep touchBegin = ' + this.touchBegin);
        if (this.touchBegin) return;

        this.graphicsNode.getComponent('mmGraphics').clearDraw();
        for (let _index = 0; _index <= this.step; _index++) {
            const _element = this.arr[_index];
            if (null!=_element) {
                cc.log("updateStep _index  "+_index);
                this.graphicsNode.getComponent('mmGraphics').setPos(_index);
                this.node.x = _element.x;
                this.node.y = _element.y;
            }
        }

        this.step--;
        if (this.step<this.arrIndex) {
            this.unschedule(this.updateBackStep);
            this.step = 1;
            // this.handNode.active = false;
            this.handTween();
        }
    },

    //描摹完成
    drawEnd() {
        cc.log('drawEnd');
        this.unschedule(this.updateGuide);
        this.touchBegin = false;
        this.gameOver = true;
        // 停止所有正在运行的tween动画
        this.stopAllActions();

        if (null==this.handNode2) {
            const particlePrefab = cc.instantiate(this.particlePrefab);
            particlePrefab.active = true;
            this.guijiNode.addChild(particlePrefab);
            this.showHand(false);

            this.scheduleOnce(() => {
                cc.log('drawEnd ClearDraw');
                particlePrefab.removeFromParent();
                this.resetGame(false);
                if (null!=this.handNode1) {
                    this.handNode1.getComponent('mmTouch').resetGame(true);
                }
            }, 5);
        } else {
            this.handNode2.active = true;
            this.graphicsNode2.active = true;
            this.showHand(false);
            this.handNode2.getComponent('mmTouch').showHand(true);
        }
    },

    showHand(isShowHand) {
        this.handIconNode.active = false;
        if (isShowHand) {
            this.schedule(this.updateGuide, 10);
            this.handIconNode.active = true;
            this.handTween()
        } else {
            this.stopAllActions(); 
        }
    },

    resetGame(isShowHand) {
        if (null!=this.handNode2) {
            this.handNode2.active = false;
            this.graphicsNode2.active = false;
            this.handNode2.getComponent('mmTouch').resetGame(false);
        }
        this.graphicsNode.getComponent('mmGraphics').clearDraw();
        this.graphicsNode.getComponent('mmGraphics').setPos(1);
        const _element = this.arr[1];
        this.node.x = _element.x;
        this.node.y = _element.y;
        this.gameOver = false;
        this.handIconNode.active = true;
        this.arrIndex = 1;
        this.stepCount = this.stepCountMax;
        this.step = 1;
        this.showHand(isShowHand);
        this.initAngle();
    },

});